<template>
  <div id="app">
    <toolbar v-on:logincheck="loginclick"></toolbar>
    <login v-bind:check="check" v-on:cancel="cancel" v-on:login="login"></login>
    <button id="cmd" v-on:click="savepdf">Generate PDF</button>
  </div>
</template>

<script>
  import * as jspdf from 'jspdf'
  import html2canvas from 'html2canvas'
  import Toolbar from './components/Toolbar'
  import Login from './components/Login'

export default {
  name: 'App',
  components: { Toolbar, Login },
  data () {
    return {
      user_data: [],
      check: false
    }
  },
  created() {
    this.$http({
      method: 'get',
      url: '/user'
    }).then((response) =>{
        console.log(response.data);
        this.user_data.push(response.data);
    })
  },
  methods: {
    savepdf: function(){
      const doc = new jspdf();
      var element = document.getElementById("#app");
      html2canvas(document.querySelector("#app")).then(canvas =>{
        var image = canvas.toDataURL("image/png");
        doc.addImage(image, 'JPEG', 15,40, 100, 100)
        doc.save("simple_test.pdf")
        });
    },
    login: function(val) {
      console.log(val.id);
      this.$http.post('/users', val).then()
    },
    loginclick: function() {
      console.log("on");
      this.check = true;
    },
    cancel: function() {
      this.check = false;
    }
  }
}
</script>

<style>
#app {
  text-align: center;
}
</style>



